:root {
    --color-background: #0d1117;
    --cyan: #64ffda;
    --color-link-on-white: #0077ff;
    --slate: #ccd6f6;
    --light-slate: #a8b2d1;
    --dark-slate: #8892b0;
    --navy: #0a192f;
    --navy-shadow-hover: rgba(0,0,0,.8);
    --navy-shadow: rgba(0,0,0,.5);
    --font-mono: "Fira Code",monospace;
    --font-sans: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
    --fz-xxs: 11px;
    --fz-sm: 14px;
    --fz-m: 16px;
    --fz-xl: 20px;
    --fz-xl: 24px;
    --fz-title: 32px;
    --pad1x: 10px;
    --pad2x: 20px;
    --pad3x: 30px;
    --pad4x: 40px;
    --box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    --transition: all 0.35s cubic-bezier(0.645,0.045,0.355,1);
    --border-radius: 8px;
}

#Title{
    font-size: var(--fz-xl);
    font-weight: 700;
}

.App{
    font-family: 'Roboto';
    background: var(--color-background);
}

.leaflet-container{
    z-index: 1;
    background: #090909;
    transition: all 0.9s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.leaflet-popup-content-wrapper{
    width: 135px;
}

.StationName{
    text-align: center;
    width: 100%;
    margin: 0;
}

.address{
    text-align: center;
    position: relative;
    color: #a1a1a1;
    font-size: 9px;
    margin: 0px;
}

.circle{
    margin-top: 10px;
    margin-bottom: 10px;
    position: relative;
    text-align: left;
    height: 30px;
    width: 100%;
}

.iconText{
    width: 30px;
    text-align: center;
    color: white;
    position: absolute;
    top: -8px;
    font-weight: 700 !important;
    left: 0;
}

.price{
    width: 60px;
    text-align: center;
    position: absolute;
    top: 25%;
    left: 35px;
}

.priceRed{
    background-color: rgba(255, 0, 0, 0.2)
}
.priceGreen{
    background-color: rgba(0, 255, 0, 0.2)
}

.circleA95 {fill: #13ab18;}
.circleA98{fill: #13ab18;}
.circleD{fill: black;}
.circleLPG{fill: red;}

.ChangeLink{
    color: var(--color-link-on-white) !important;
    font-size: 11px;
    display: block;
    width: 100%;
    text-align: center;
}

.updated{
    margin-top: var(--pad1x);
    text-align: center;
    color: #a1a1a1;
    font-size: 9px;
}


#Menu{
    position: absolute;
    display: flex;
    flex-direction: row;
    top: 0;
    left: -320px;
    transition: var(--transition);
    background: rgb(26, 26, 26);
    width: 400px;
    height: 100vh;
    overflow-y: scroll;
    z-index: 2;
}
    #StationPanel{
        font-family: 'Roboto';
        height: 100vh;
        width: 320px;
    }

    #PricePanel{
        position: relative;
        display: flex;
        flex-direction: column;
        width: 60px;
        height: 100%;
        right: 0;
        color: whitesmoke; 
        text-align: center;
        padding: var(--pad1x);
        font-size: var(--fz-xxs);
        font-weight: 300 !important;
    }
        
        .circleDisplay{
            padding-top: 10px;
            position: relative;
            width: 100%;
        }

        .openMenuButton{
            right: 0;
            width: 100%;
            height: 42px;
            opacity: 0;
        }
            .fa-bars{
                color: rgb(255, 255, 255);
                text-align: center;
                font-size: 42px;
                width: 100%;
                padding: var(--pad1x) 0 ; 
            }

        .iconTextDisplay{
            position: relative;
            color: white;
            font-size: var(--fz-xl);
            width: 100%;
            text-align: center;
            z-index: 3;
        }

        .circleDisplay svg{
            position: absolute;
            top: 26px;
            left: 0;
            z-index: 1;
        }

#Title {
    color: white;
    text-align: center;
}

#chart{
    width: 100%;
    height: 200px;
}

footer{
    text-align: center;
    font-size: 12px;
    color: white;
    bottom: 0;
    width: 100%;
}
footer a {
    color: white;
}

.minPrice{
    cursor: pointer;
}
.redText{
    color: rgb(255, 130, 130);
    padding: 0 var(--pad1x) var(--pad1x) var(--pad1x);
}
.betaText{
    color: yellow;
}

.lds-dual-ring.small {
    display: none;
    height: 12px;
    width: 12px;
}
.lds-dual-ring.small::after{
    border: 3px solid #fff;
    border-color: #fff transparent #fff transparent;
    margin: -3px; /* need this cause of border */
}
.lds-dual-ring.small.blue::after{
    border: 3px solid var(--cyan);
    border-color: var(--color-link-on-white) transparent var(--color-link-on-white) transparent;
}
.lds-dual-ring {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    width: 26px;
    height: 26px;
    z-index: 2;
  }
    .lds-dual-ring:after {
        content: " ";
        display: block;
        width: 100%;
        height: 100%;
        margin: 6px;
        border-radius: 50%;
        border: 6px solid #fff;
        border-color: #fff transparent #fff transparent;
        animation: lds-dual-ring 1.2s linear infinite;
    }

#ErrorDiv{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(49, 49, 49);
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    padding: 20px;
    color: white;
    text-align: center;
    font-family: var(--font-sans);
    z-index: 2;
    line-height: 24px;
}
    #ErrorDiv h2{
        margin-top: 0;
        font-size: var(--fz-xl);
        color: red;
    }

a {
    cursor: pointer;
    text-decoration: none;
}

.aeffect {
    display: inline-block;
    position: relative;
    color: var(--cyan);
    }
    .aeffect::after {
        content: '';
        position: absolute;
        width: 100%;
        transform: scaleX(0);
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: var(--cyan);
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
    }
    .aeffect:hover::after {
        transform: scaleX(1);
        transform-origin: bottom left;
    }

@keyframes lds-dual-ring {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}

/* SCROLLBAR */

*::-webkit-scrollbar {
  width: 0px;
  opacity: 0;
}
